import React from "react";
import { useParams, Link, NavLink, Routes, Route, Navigate } from "react-router-dom";
import Rating from "@mui/material/Rating";
import { Helmet } from "react-helmet";
import { useDispatch, useSelector } from "react-redux";

import { addProduct, removeProduct } from "../../redux/slices/orderingSlice";
import {
	addToFavorites,
	removeFromFavorites,
	addToCompare,
	removeFromCompare,
} from "../../redux/slices/productsStatusSlice";
import useGoogleSheetsAPI from "../../hooks/useGoogleSheetsAPI";
import ProductPageSkeleton from "../../components/ProductPageSkeleton/ProductPageSkeleton";
import ProductSlider from "../../components/ProductSlider/ProductSlider";
import ProductsSection from "../../components/ProductsSection/ProductsSection";
import Page404 from "../Page404/Page404";
import ProductPageTabDescription from "../../components/ProductPageTabDescription/ProductPageTabDescription";
import ProductPageTabCharacteristics from "../../components/ProductPageTabCharacteristics/ProductPageTabCharacteristics";
import ProductPageTabReviews from "../../components/ProductPageTabReviews/ProductPageTabReviews";

import "./productPage.scss";

const ProductPage = () => {
	const productTabsRef = React.useRef();
	const productTabsHeaderListRef = React.useRef();
	const { productId } = useParams();
	const { getProduct, loading, error } = useGoogleSheetsAPI();
	const [product, setProduct] = React.useState({
		title: null,
		price: {},
		discount: null,
		images: [],
		description: [],
		characteristics: [],
		reviews: {},
		isLoaded: false,
	});
	const dispatch = useDispatch();
	const isProductInCart = useSelector((state) =>
		state.ordering.productsInCart.some((product) => product.productId === productId)
	);
	const isProductInFavorites = useSelector((state) =>
		state.productsStatus.favorites.items.some((item) => item === productId)
	);
	const isProductInCompare = useSelector((state) =>
		state.productsStatus.inCompare.items.some((item) => item === productId)
	);

	React.useEffect(() => {
		loadProduct();
	}, [productId]);

	const loadProduct = async () => {
		const result = await getProduct(productId);

		if (result) {
			setProduct({ ...result, isLoaded: true });
		}
	};

	const scrollToReviews = () => {
		window.scrollTo({
			top: productTabsRef.current.offsetTop,
			behavior: "smooth",
		});
		productTabsHeaderListRef.current.scroll({
			left: 200,
			behavior: "smooth",
		});
	};

	const toggleProductToCart = () => {
		if (isProductInCart) {
			dispatch(removeProduct({ productId }));
		} else {
			dispatch(addProduct({ ...product, productId, itemsInCart: 1 }));
		}
	};

	const toggleProductToFavorites = () => {
		if (isProductInFavorites) {
			dispatch(removeFromFavorites({ productId }));
		} else {
			dispatch(addToFavorites({ productId }));
		}
	};

	const toggleProductToCompare = () => {
		if (isProductInCompare) {
			dispatch(removeFromCompare({ productId }));
		} else {
			dispatch(addToCompare({ productId }));
		}
	};

	const onClickFastBuy = () => {
		if (!isProductInCart) {
			dispatch(addProduct({ ...product, productId, itemsInCart: 1 }));
		}
	};

	const { title, price, discount, images, description, characteristics, reviews } = product;

	return (
		<>
			<Helmet>
				<title>{`${title || "Product page"} - Smart Technics`}</title>
				<meta
					name="description"
					content={
						"You can check a description, photos, characteristicts, reviews, prices for the product" + title
					}
				/>
			</Helmet>
			<main>
				<div className="container">
					{loading && <ProductPageSkeleton />}
					{error && <Page404 />}
					{!loading && !error && product.isLoaded && (
						<section className="page-product">
							<div className="breadcrumbs">
								<ul className="breadcrumbs__list">
									<li className="breadcrumbs__item">
										<Link className="breadcrumbs__link" to="/" aria-label="go to Homepage">
											Home
										</Link>
									</li>
									<li className="breadcrumbs__item">
										<Link
											className="breadcrumbs__link"
											to="/catalogue"
											aria-label="go to Catalogue"
										>
											Catalogue
										</Link>
									</li>
									<li className="breadcrumbs__item">
										<Link
											className="breadcrumbs__link"
											to="/catalogue"
											aria-label="go to Gyroscooters"
										>
											Gyroscooters
										</Link>
									</li>
									<li className="breadcrumbs__item">
										<span className="breadcrumbs__link">{title}</span>
									</li>
								</ul>
							</div>
							<div className="page-product__product product-page">
								<div className="product-page__body-top">
									<div className="product-page__sliders-body">
										<ProductSlider title={title} images={images} />
									</div>
									<div className="product-page__content">
										<h3 className="product-page__title title">{title}</h3>
										<div className="product-page__content-box">
											<div className="product-page__actions user-actions">
												<div className="user-actions__evaluation">
													<div className="user-actions__rating rating">
														<div className="rating__body">
															<Rating
																className="rating__body"
																name="hover-feedback"
																value={reviews.rating ? +reviews.rating : 0}
																precision={0.1}
																readOnly
																size="large"
															/>
														</div>
														{reviews.rating && (
															<div className="rating__value">
																{(+reviews.rating).toFixed(2)}
															</div>
														)}
													</div>
													<Link
														className="user-actions__reviews"
														to="reviews"
														onClick={scrollToReviews}
														aria-label="go to Tab 'Reviews' for this product"
													>
														<svg
															width="24"
															height="24"
															viewBox="0 0 24 24"
															fill="none"
															xmlns="http://www.w3.org/2000/svg"
														>
															<path
																d="M21 15C21 15.5304 20.7893 16.0391 20.4142 16.4142C20.0391 16.7893 19.5304 17 19 17H7L3 21V5C3 4.46957 3.21071 3.96086 3.58579 3.58579C3.96086 3.21071 4.46957 3 5 3H19C19.5304 3 20.0391 3.21071 20.4142 3.58579C20.7893 3.96086 21 4.46957 21 5V15Z"
																stroke="currentColor"
																strokeWidth="2"
																strokeLinecap="round"
																strokeLinejoin="round"
															/>
														</svg>
														<span>({reviews.count || 0})</span>
													</Link>
												</div>
												<div className="user-actions__btns">
													<button
														className={
															"user-actions__btn user-actions__btn--favorite" +
															(isProductInFavorites ? " in-favorites" : "")
														}
														onClick={toggleProductToFavorites}
														aria-label="add the product to a list 'Favorites'"
													/>
													<button
														className={
															"user-actions__btn user-actions__btn--compare" +
															(isProductInCompare ? " in-compare" : "")
														}
														onClick={toggleProductToCompare}
														aria-label="add the product to a list 'Comparement'"
													/>
												</div>
											</div>
											<div className="product-page__purchase purchase-product">
												<div className="purchase-product__item">
													{price.old && (
														<span className="purchase-product__old-price">{price.old}</span>
													)}
													{discount && (
														<p className="purchase-product__discount">
															<span className="purchase-product__dicsount-percent">
																-{discount.percent}%
															</span>{" "}
															— $ {discount.amount}
														</p>
													)}
													<div className="purchase-product__current-price">
														{price.current}
													</div>
												</div>
												{isProductInCart ? (
													<button
														className="purchase-product__button purchase-product__button--in-cart"
														onClick={toggleProductToCart}
														aria-label="remove the product from a Cart"
													>
														In cart
													</button>
												) : (
													<button
														className="purchase-product__button purchase-product__button--to-cart"
														onClick={toggleProductToCart}
														aria-label="add the product from a Cart"
													>
														Add to cart
													</button>
												)}
												<Link
													className="purchase-product__button purchase-product__button--one-click"
													to="/ordering"
													onClick={onClickFastBuy}
													aria-label="add the product to the Cart and navigate to the Cart"
												>
													Buy in one click
												</Link>
											</div>
										</div>
										<ul className="product-page__shipping shipping-product">
											<li className="shipping-product__item shipping-product__item--delivery">
												<p className="shipping-product__title">Delivery</p>
												<p className="shipping-product__text">
													Delivery within CA is going to be within 2 hours and for free. The
													cost of delivery to other cities needs to be clarified.
												</p>
											</li>
											<li className="shipping-product__item shipping-product__item--payment">
												<p className="shipping-product__title">Payment</p>
												<p className="shipping-product__text">
													We accept both cash and non-cash payments. Payment by electronic
													wallets is possible.
												</p>
											</li>
										</ul>
									</div>
								</div>
								<div className="product-page__body-bottom">
									<div className="product-page__tabs product-tabs" ref={productTabsRef}>
										<div className="product-tabs__header">
											<ul
												className="product-tabs__header-list"
												role="tablist"
												ref={productTabsHeaderListRef}
											>
												<li className="product-tabs__header-item" role="tab">
													<NavLink to="description" replace="true">
														Description
													</NavLink>
												</li>
												<li className="product-tabs__header-item" role="tab">
													<NavLink to="characteristics" replace="true">
														Characteristics
													</NavLink>
												</li>
												<li className="product-tabs__header-item" role="tab">
													<NavLink to="reviews" replace="true">
														Reviews <span>({reviews.count || 0})</span>
													</NavLink>
												</li>
											</ul>
										</div>
										<div className="product-tabs__body">
											<ul className="product-tabs__body-list">
												<Routes>
													<Route
														index
														element={
															<Navigate
																to={`/products/${productId}/description`}
																replace="true"
															/>
														}
													/>
													<Route
														path="description"
														element={
															<ProductPageTabDescription
																title={title}
																description={description}
															/>
														}
													/>
													<Route
														path="characteristics"
														element={
															<ProductPageTabCharacteristics
																title={title}
																characteristics={characteristics}
															/>
														}
													/>
													<Route
														path="reviews"
														element={
															<ProductPageTabReviews title={title} reviews={reviews} />
														}
													/>
												</Routes>
											</ul>
										</div>
									</div>
								</div>
							</div>
							<ProductsSection title="We recommend" />
						</section>
					)}
				</div>
			</main>
		</>
	);
};

export default ProductPage;
