import { useParams } from 'react-router-dom';
import { Rating } from '@mui/material';

import './productPageTabReviews.scss';

const ProductPageTabReviews = ({title, reviews}) => {

    const {content} = reviews;

    return (
        <li
            className="product-tabs__body-item product-tab fade"
            role="tabpanel"
        >
            <h5 className="product-tab__title">Reviews for {title}</h5>
            <div className="product-tab__review">
                { content 
                    ? <ul className="product-tab__review-content content-reviews">
                        {content.map(({name, date, title, body, rating}) => (
                            <li className="content-reviews__item content-review" key={`${name}_${date}`}>
                                <div className="content-review__header">
                                    <div className="content-review__header-img">{name.slice(0, 1).toUpperCase()}</div>
                                    <div className="content-review__header-username">{name}</div>
                                    <div className="content-review__header-date">{date}</div>
                                    <div className="content-review__header-rating rating"
                                        data-ajax='' method="GET">
                                        <div className="rating__body">
                                            <Rating
                                                className='rating__body'
                                                name="hover-feedback"
                                                value={+rating}
                                                precision={0.1}
                                                readOnly
                                                size="large"
                                            />
                                        </div>
                                        <div className="rating__value">{`${rating}.0`}</div>
                                    </div>
                                </div>
                                <div className="content-review__body">
                                    <p className="content-review__body-title">{title}</p>
                                    <div className="content-review__body-text">{body}</div>
                                </div>
                            </li>
                        ))}
                    </ul>
                    : <p className="product-tab__review-appsent">There are no reviews for the product.</p>
                }
                <div className="product-tab__review-actions actions-review">
                    <p className="actions-review__title">Leave a feedback about the product</p>
                    <p className="actions-review__text">Make it easier for other buyers to choose the right product</p>
                    <button className="actions-review__btn">Leave a review</button>
                </div>
            </div>
        </li>
    );
}

export default ProductPageTabReviews;