import React from "react";
import { Link, useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";

import "./page404.scss";

const Page404 = () => {
	const location = useLocation();

	return (
		<div className="page404">
			<Helmet>
				<title>404 - Smart Technics</title>
				<meta name="description" content={"It is a 404 page. Sorry, but something wrong has happened"} />
			</Helmet>
			<section className="page404__section">
				<div className="container">
					<div className="page404__inner">
						<h1 className="page404__title">404</h1>
						<p className="page404__description">
							Page <span>"{location.pathname}"</span> does not exist.
						</p>
						<Link className="page404__btn" to="/">
							Back to homepage
						</Link>
					</div>
				</div>
			</section>
		</div>
	);
};

export default Page404;
