import { useParams } from 'react-router-dom';

import './productPageTabDescription.scss';

const ProductPageTabDescription = ({title, description}) => {

    return (
        <li 
            className="product-tabs__body-item product-tab fade"
            role="tabpanel"
        >
            <h5 className="product-tab__title">Description of {title}</h5>
            {description.map(item => <p className="product-tab__text" key={item.slice(0, 15)}>{item}</p>)}
        </li>
    )
};

export default ProductPageTabDescription;