import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, EffectFade, Thumbs } from 'swiper';

import 'swiper/scss';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';
import 'swiper/css/effect-fade';
import './productSlider.scss';

const ProductSlider = ({title, images}) => {
    const [thumbsSwiper, setThumbsSwiper] = React.useState(null);

    return (
        <div className="product-page__slider-main slider-product">
            <Swiper
                className="slider-product__swiper"
                modules={[Navigation, Pagination, EffectFade, Thumbs]}
                spaceBetween={10}
                slidesPerView={1}
                allowTouchMove
                loop={false}
                speed={500}
                effect='fade'
                thumbs={{swiper: thumbsSwiper}}
            >
                {images?.length > 0 && images.map(({webp, src}) => (
                    <SwiperSlide className="slider-product__slide" key={src}>
                        <picture className="slider-product__image">
                            <source srcSet={webp} type="image/webp"/>
                            <img className="slider-product__image" src={src} alt={title} />
                        </picture>
                    </SwiperSlide>)
                )}
            </Swiper>
            <div className="product-page__slider-thumbs slider-thumbs">
                <Swiper
                    className="slider-thumbs__swiper"
                    modules={[Navigation, Thumbs]}
                    watchSlidesProgress
                    onSwiper={setThumbsSwiper}
                    slidesPerView={3}
                    allowTouchMove
                    navigation
                    spaceBetween={8}
                    centeredSlides
                    slideToClickedSlide
                >
                    {images?.length > 0 && images.map(({webp, src}) => (
                        <SwiperSlide className="slider-thumbs__slide" key={src}>
                            <picture className="slider-thumbs__image">
                                <source srcSet={webp} type="image/webp"/>
                                <img className="slider-thumbs__image" src={src} alt={title} />
                            </picture>
                        </SwiperSlide>)
                    )}
                </Swiper>
            </div>
        </div>
    )
}

export default ProductSlider;