import { useParams } from 'react-router-dom';

import './productPageTabCharacteristics.scss';

const ProductPageTabCharacteristics = ({title, characteristics}) => {

    return (
        <li 
            className="product-tabs__body-item product-tab fade"
            role="tabpanel"
        >
            <h5 className="product-tab__title">Characteristics of {title}</h5>
            <ul className="product-tab__characteristics">
                {characteristics.map(({title, value, unit}) => (
                    <li className="product-tab__characteristic" key={title}>
                        <div className="product-tab__characteristic-title">{title}:</div>
                        <div className="product-tab__characteristic-value">{value + (unit ? ` ${unit}` : '')}</div>
                    </li>    
                ))}
            </ul>
        </li>
    );
}

export default ProductPageTabCharacteristics;