import React from "react";
import { Link } from "react-router-dom";

import ProductCard from "../ProductCard/ProductCard";
import ProductsSectionSkeleton from "../ProductsSectionSkeleton/ProductsSectionSkeleton";
import ProductsSectionError from "../productsSectionError/ProductsSectionError";
import useGoogleSheetsAPI from "../../hooks/useGoogleSheetsAPI";

import "./productsSection.scss";

const ProductsSection = ({ title }) => {
	const [products, setProducts] = React.useState([]);
	const { getProducts, loading, error } = useGoogleSheetsAPI();

	const loadProducts = async () => {
		const result = await getProducts(1, 4);
		setProducts((prev) => [...prev, ...result]);
	};
	React.useEffect(() => {
		loadProducts();
	}, []);

	return (
		<section className="products">
			<div className="products__header">
				<h3 className="products__title">{title}</h3>
				<Link className="products__link" to="/catalogue">
					More products
				</Link>
			</div>
			<div className="products__body">
				<ul className="products__row">
					{loading ? <ProductsSectionSkeleton /> : null}
					{products.length > 0
						? products.map((product) => <ProductCard key={product.productId} {...product} />)
						: null}
					{error ? <ProductsSectionError /> : null}
				</ul>
			</div>
		</section>
	);
};

export default ProductsSection;
