import './productPageSkeleton.scss';

const ProductPageSkeleton = () => {
    return (
        <div className="product-page-skeleton">
            <div className="product-page-skeleton__breadcrumbs"></div>
            <div className="product-page-skeleton__product">
                <div className="product-page-skeleton__body-top">
                    <div className="product-page-skeleton__slider">
                        <div className="product-page-skeleton__slider-photo">
                            <div className="product-page-skeleton__slider-img"></div>
                        </div>
                        <div className="product-page-skeleton__slider-controls"></div>
                    </div>
                    <div className="product-page-skeleton__content">
                        <div className="product-page-skeleton__title"></div>
                        <div className="product-page-skeleton__title"></div>
                        <div className="product-page-skeleton__content-box">
                            <div className="product-page-skeleton__actions">
                                <div className="product-page-skeleton__evaluation"></div>
                                <div className="product-page-skeleton__btns">
                                    <div className="product-page-skeleton__btn"></div>
                                    <div className="product-page-skeleton__btn"></div>
                                    <div className="product-page-skeleton__btn"></div>
                                </div>
                            </div>
                        </div>
                        <div className="product-page-skeleton__shipping">
                            <div className="product-page-skeleton__shipping-item"></div>
                            <div className="product-page-skeleton__shipping-item"></div>
                        </div>
                    </div>
                </div>
                <div className="product-page-skeleton__body-bottom">
                    <div className="product-page-skeleton__tabs">
                        <div className="product-page-skeleton__tabs-header"></div>
                        <div className="product-page-skeleton__tabs-body">
                            <div className="product-page-skeleton__tabs-text"></div>
                            <div className="product-page-skeleton__tabs-text"></div>
                            <div className="product-page-skeleton__tabs-text"></div>
                            <div className="product-page-skeleton__tabs-text"></div>
                            <div className="product-page-skeleton__tabs-text"></div>
                            <div className="product-page-skeleton__tabs-text"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ProductPageSkeleton;